html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

button {
  border: none;
}

:root {
  --primaryColor: #fff;
  --navColor1: #edb703;
  --navColor2: #530ea2;
  --navColor3: #ffda5f;
  --navColor4: #2119a7;
  --navColor5: red;
}

body {
  @import url("https://fonts.googleapis.com/css2?family=Spartan:wght@100;200;300;400;500&display=swap");
  font-family: "Spartan", sans-serif;
}

.site-wrapper {
  position: fixed;
  background-image: url(/dbz-music-player/img/bg.jpeg);
  background-repeat: no-repeat;
  background-position: center right;
  background-size: cover;
  color: white;

  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.overlay {
  position: absolute;
  z-index: 1;
  background: white;
  opacity: 0.5;
  overflow: hidden;

  height: 110vh;
  width: 100vw;
}

.main-content {
  position: relative;
  z-index: 2;

  display: flex;
  flex-direction: column;

  margin: 3rem 0 0 0;
  min-height: 100vh;

  display: flex;
  justify-content: center;
  align-items: center;

  font-size: 1.2rem;
}

.player-container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  height: 47rem;
  width: 40rem;
  background: linear-gradient(to right, #c31432, #4e0713);
  border-radius: 2rem;
  box-shadow: 0 15px 30px 5px rgba(0, 0, 0, 0.3);
}

.img-container {
  position: relative;
  top: -5rem;
  height: 30rem;
  width: 30rem;

  img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    border-radius: 2rem;
    box-shadow: 0 5px 30px 5px rgba(0, 0, 0, 0.3);
  }
}

h2 {
  position: relative;
  top: -2.5rem;
  font-size: 25px;
  text-align: center;
  margin: 0 1.5rem 0 0 !important;
}

h3 {
  position: relative;
  top: -2.5rem;

  font-size: 20px;
  text-align: center;
  font-weight: 400;
  margin: 5px 0 0;
}

/* Progress */
.progress-container {
  position: relative;
  top: -3rem;
  background: #fff;
  border-radius: 5px;
  cursor: pointer;
  margin: 40px 20px;
  height: 4px;
  width: 90%;
}

.progress {
  background: #242323;
  border-radius: 5px;
  height: 100%;
  width: 0%;
  transition: width 0.1s linear;
}

.duration-wrapper {
  position: relative;
  top: -25px;
  display: flex;
  justify-content: space-between;
}

/* Controls */
.player-controls {
  position: relative;
  top: -4.5rem !important;
  left: 2.5rem;
  width: 200px;
}

.fas {
  font-size: 30px;
  color: rgb(255, 255, 255);
  margin-right: 30px;
  cursor: pointer;
  user-select: none;

  &:hover {
    filter: brightness(80%);
  }
}

.main-button {
  font-size: 40px;
  position: relative;
  top: 3px;
}

.ayo {
  margin-top: 2rem;
  font-size: 1.5rem;
  text-decoration: none;
  color: black;
  transition: color 0.5s ease-in-out, transform 0.5s ease;
  font-style: bold;

  &:hover {
    color: red;
    transform: scale(1.025);
  }
}

/* Media Query: iPhone (Vertical) */
@media screen and (max-width: 428px) {
  .player-container {
    width: 90vw;
  }

  .img-container {
    width: 27rem;
  }

  .player-controls {
    top: -10px;
  }
}
